import { useEffect, useState } from 'react';
import styled from 'styled-components';
import { animated, useSpring } from 'react-spring';

const bgColor = "#8475EB";

const TimeContainer = styled(animated.div)`
    height: ${props => (props.isOnPhone ? "2.5rem" : "4rem")};
    width: ${props => (props.isOnPhone ? "8rem" : "10rem")};
    background-color: ${bgColor};
    color: ghostwhite;
    font-weight: bold;
    font-size: ${props => (props.isOnPhone ? "1.2rem" : "1.5rem")};
    position: absolute;
    padding-top: ${props => (props.isOnPhone ? "0.5rem" : "0rem")};
    padding-bottom: 1rem;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    box-shadow: 0px 0px 8px gray;

`;

const Time = ({ timezone, isOnPhone }) => {


    const [date, setDate] = useState("");

    let addHour = (timezone / 3600) || 0;
    let addMinute = 0;

    addHour = addHour.toString();
    if(addHour.includes(".")){
        const index = addHour.indexOf(".");
        addMinute = addHour.substring(index+1, addHour.length);
        addHour = parseInt(addHour.substring(0, index));

        switch(parseInt(addMinute)){
            case 25:
                addMinute = 15;
                break;
            case 50:
            case 5:
                addMinute = 30;
                break;
            case 75:
                addMinute = 45;
                break;
            default: addMinute = 0;
        };
    }

    let hour = new Date().getUTCHours() + parseInt(addHour);
    let minute = new Date().getUTCMinutes() + addMinute;
    let second = new Date().getUTCSeconds();
    let time = "";

    if(minute > 59) {
        minute -= 60;
        if( (hour+1) > 23 ) hour = 0;
        else hour++;
    }
    if(hour > 23) hour -= 24;

    useEffect(() => {
        const updateTime = setInterval(() => {
            if((second + 1) >= 60){
                second = 0;
                if((minute + 1) >= 60){
                    minute = 0;
                    if((hour + 1) >= 24){
                        hour = 0;
                    } else hour++;
                } else minute++;
            } else second++;
            
            time = (((hour<10)?("0"+hour):(hour)) + ":" + ((minute<10)?("0"+minute):(minute)) + ":" + ((second<10)?("0"+second):(second)));
            setDate(time);
        }, 1000);


        return () => {clearInterval(updateTime)};
    }, []);

    const moveOut = useSpring({
        from: { top: (isOnPhone ? "3rem" : "4rem") },
        to: { top: (isOnPhone ? "1rem" : "0rem") },
        delay: 1500,
    });

    return (
        <TimeContainer style={moveOut} isOnPhone={isOnPhone}>
            {date}
        </TimeContainer>
    )
}

export default Time
