import React from 'react';
import styled from 'styled-components';

const Prop = styled.div`
    height: ${props => (props.isOnPhone ? "1.7rem" : "2.5rem")};
    width: 100%;
    position: relative;
    margin-bottom: 0.5rem; 
`;

const PropName = styled.label`
    position: absolute;
    left: 0.5rem;
    color: ghostwhite; 
    font-size: ${props => (props.isOnPhone ? "1.1rem" : "2rem")};
    font-weight: bold;
    text-transform: capitalize;
`;

const PropValue = styled.label`
    position: absolute;
    left: 14.5rem;
    left: ${props => (props.isOnPhone ? "8.5rem" : "14.5rem")};
    color: ghostwhite; 
    font-size: ${props => (props.isOnPhone ? "1rem" : "2rem")};
    text-transform: capitalize;
`;

const Property = ({ name, value, add, isOnPhone }) => {
    if(!add) add = "";

    return (
        <Prop isOnPhone={isOnPhone}>
            <PropName isOnPhone={isOnPhone}>{name + ":"}</PropName>
            <PropValue isOnPhone={isOnPhone}>{value + add }</PropValue>
        </Prop>
    )  
}

export default Property
