import styled from "styled-components"

const Container = styled.div`
    width: 100%;
    height: 3.5rem;
    margin-bottom: 1rem;
    position: relative;
    margin-right: 3rem;
`;

const LeftDiv = styled.div`
    height: 100%;
    width: 20%;
    background-color: blueviolet;
    color: white;
    font-weight: bold;
    margin-right: 0.5%;
    min-width: 5px;
    display: inline-block;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    position: absolute;
    text-align: center;
    line-height: 3.5rem;
    top: 0;
    left: 0;
    /* box-shadow: 0px 0px 3px black; */
`;

const RightDiv = styled.div`
    height: 100%;
    width: 79.5%;
    display: inline-block;
    background-color: ghostwhite;
    border: #8475EB 3px solid;
    font-weight: bold;
    color: #8475EB;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    position: absolute;
    top: 0;
    right: 0;
    text-align: center;
    line-height: 3.2rem;
`;
    

const WeatherItem = ({ item, unit } ) => {
    const date = item.dt_txt.substring(11, item.dt_txt.length - 3) + " / " + item.dt_txt.substring(0, 10);
    const desc = item.weather[0].description;
    const temp = (unit === "C") ? ((item.main.temp - 273.15).toFixed(2) + "°C") :  (((item.main.temp - 273.15) *9/5 + 32).toFixed(2) + "°F");
    const feelsLike = (unit === "C") ? ((item.main.feels_like - 273.15).toFixed(2) + "°C") : (((item.main.feels_like - 273.15) *9/5 + 32).toFixed(2) + "°F");
    
    return (
        <Container>
            <LeftDiv> <label> {date} </label> </LeftDiv> 
            <RightDiv>
                <label style={{ position: "absolute", left: "3rem", textTransform: "capitalize"}}> {desc} </label>     
                <label style={{ marginRight: "4rem" }}> Temp: {temp} </label>     
                <label style={{ position: "absolute", right: "3rem" }}> Feels Like: {feelsLike} </label>     
            </RightDiv>
        </Container>
    )
}

export default WeatherItem
