import { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import { animated, useSpring } from 'react-spring';
import { findCity } from "../OCLFunctions";
import SuggestBox from './SuggestBox';
import { Link } from 'react-router-dom';

const minWidth = "10vw";
const manWidth = "25vw";

const InputContainer = styled(animated.div)`
    width: 99vw;
    height: 2.6rem;
    position: fixed;
    bottom: 0;
    background-image: linear-gradient(-135deg , blueviolet 60%, #3f1469); 
    /* background-color: blueviolet; */
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 15px gray;
    z-index: 1;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    padding-left: 1.2rem;
`;

const InputBox = styled.input`
    width: ${props => props.expand ? manWidth : minWidth};
    /* height: 2rem; */
    height: 80%;
    border: ${props => props.expand ? "blueviolet 1px solid" : "none"};
    border-radius: 15px;
    text-align: center;
    font-weight: bold;
    font-size: 1.5rem;
    color: #5d1e97;
    background-color: ghostwhite;
    position: absolute;
    right: 2rem;
    /* bottom: 0.3rem; */
    box-shadow: inset 0px 0px 5px gray;
    transition: width 150ms ease-out, border-radius 0ms ease-out;

    &::placeholder {
        color: #8475EB;
    }

    &:focus, &:active{
        border: #8475EB 1px solid;
        outline: none;
        width: 25vw;
    }
`;

const LinkContainer = styled.div`
    height: 100%;
    width: auto;
    /* background-color: red; */
    padding-inline: 0.7rem;
    font-size: 1.5rem;
    text-transform: capitalize;
    line-height: 2.8rem;
    transition: font-weight 150ms ease-out;

    &:hover {
        /* font-weight: bold; */
        text-decoration: underline;
    }
`;


const SearchBar = ({ setName, ready, visible }) => {
    const [text, setText] = useState("");
    const [cities, setCities] = useState([]);
    const [suggest, setSuggest] = useState(false);
    const inputBoxRef = useRef(null);
    const suggestList = useRef(null);
    const current = useRef(0);

    const moveY = useSpring({
        // from: { y: `2rem`, opacity: 0, zIndex: 2 },
        // to: { y: "0rem", opacity: 1 },
        // delay: 2500,
    });

    useEffect(() => {
        if(!visible) return;

        (async() => {
            if(!ready)
                setText("");
            
            if(text.length >= 3){
                setCities(findCity(text));
                setSuggest(true);
                inputBoxRef.current.style.borderTopRightRadius = "0px";
                inputBoxRef.current.style.borderTopLeftRadius = "0px";
            } else {
                setSuggest(false);
                inputBoxRef.current.style.borderTopRightRadius = "15px";
                inputBoxRef.current.style.borderTopLeftRadius = "15px";
            }            
        })();
    }, [text, ready]);

    useEffect(() => {
        if(!visible) return;

        const el1 = document.body.addEventListener("keydown", (e) => {
            if(e.code === "ControlLeft"){
                if(inputBoxRef.current)
                    inputBoxRef.current.focus();
            } else if(e.code === "Enter"){
                e.preventDefault();
                if(suggestList.current !== null){
                    if(current.current !== 0){
                        suggestList.current.querySelector(`div:nth-child(${current.current})`).click();
                        current.current = 0;
                    } else suggestList.current.querySelector(`div:nth-child(1)`).click();
                }
            } else if(e.code === "Backspace"){
                current.current = 0;
            } else if(e.code === "ArrowUp"){
                 e.preventDefault();

                if(suggestList.current !== null){
                    const sl = suggestList.current.querySelectorAll(`div`);
                    if(current.current > 1){
                        current.current--;

                        sl.forEach(el => {
                            el.style.color = "#5d1e97";
                            el.style.backgroundColor = "ghostwhite";
                        })
                        const currentElem = suggestList.current.querySelector(`div:nth-child(${current.current})`);
                        currentElem.style.color = "ghostwhite";
                        currentElem.style.backgroundColor = "#8475EB";
                    }
                }
            } else if(e.code === "ArrowDown"){
                e.preventDefault();

                if(suggestList.current !== null){
                    const sl = suggestList.current.querySelectorAll(`div`);
                    if(current.current < sl.length){
                        current.current++;

                        sl.forEach(el => {
                            el.style.color = "#5d1e97";
                            el.style.backgroundColor = "ghostwhite";
                        })
                        const currentElem = suggestList.current.querySelector(`div:nth-child(${current.current})`);
                        currentElem.style.color = "ghostwhite";
                        currentElem.style.backgroundColor = "#8475EB";
                    }
                }
            }
        });

        return () => document.body.removeEventListener("keydown", el1);
    }, []);

    return (
        <InputContainer style={moveY}>
            <LinkContainer> 
                <Link to="/" style={{ color: "white", textDecoration: "none", }}> now </Link> 
            </LinkContainer>
            <LinkContainer> 
                <Link to="/later" style={{ color: "white", textDecoration: "none", }}> later </Link> 
            </LinkContainer>
            <LinkContainer> 
                <Link to="/about" style={{ color: "white", textDecoration: "none", }}> about </Link> 
            </LinkContainer>
            {
                visible && <InputBox type="text" value={text} onChange={e => setText(e.target.value)} placeholder="Search" ref={inputBoxRef}  expand={suggest}/>
            }
            {suggest &&  <SuggestBox cities={cities} setName={setName} inputBox={inputBoxRef} listRef={suggestList}/>}
        </InputContainer>
    )
}

export default SearchBar
