import { useEffect, useState } from "react";
import styled from 'styled-components';
import MainCard from './component/MainCard';
import InfoCard from "./component/InfoCard";
import SearchBar from "./component/SearchBar";
import WelcomeSlide from "./component/WelcomeSlide";
import UnitSelector from "./component/UnitSelector";

const Container = styled.section`
  height: 100vh;
  max-height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow-y: hidden;
`;

function Home() {
  const [isReady, setIsReady] = useState(false);
  const [cityName, setCityName] = useState("london");
  const [city, setCity] = useState({});
  const [unit, setUnit] = useState("C");
  const [isOnPhone, setIsOnPhone] = useState(false);
  const { REACT_APP_API_KEY: apiKey } = process.env;

  const alternative = {"coord":{"lon":-0.1257,"lat":51.5085},"weather":[{"id":804,"main":"Clouds","description":"overcast clouds","icon":"04d"}],"base":"stations","main":{"temp":292.05,"feels_like":291.56,"temp_min":289.98,"temp_max":293.92,"pressure":1023,"humidity":60},"visibility":10000,"wind":{"speed":4.63,"deg":330},"clouds":{"all":90},"dt":1630073950,"sys":{"type":2,"id":2006068,"country":"GB","sunrise":1630040727,"sunset":1630090740},"timezone":3600,"id":2643743,"name":"London","cod":200};

  useEffect(() => {
    if(window.innerWidth <= 600) {
      setIsOnPhone(true);
    };

    const adjustScreen = () => {
      if(window.innerWidth <= 600) {
        setIsOnPhone(true);
      } else setIsOnPhone(false);
    };

    window.addEventListener("resize", adjustScreen);

    return () => window.removeEventListener("resize", adjustScreen);
  }, []);


  useEffect(() => {
    try {
      const url = `https://api.openweathermap.org/data/2.5/weather?q=${cityName}&appid=${apiKey}`;
      
      const getCity = async() => {
        const jsonCity = await fetch(url);

        if(jsonCity.ok){
          await jsonCity.json()
            .then(data => {
              setCity(data);
              setIsReady(true);
            });
          } else {
            console.log("error");
            setCity(alternative);
            setIsReady(true);
          }
      } 
      getCity();
    } catch (error) {
      console.log(error);
    }

  }, [cityName]);
  
  const setName = (name, input) => {
    if(name === cityName){
      return
    }

    setIsReady(false);
    input.current.value= "";
    setCityName(name);
  }

  return (
    <Container>
      <WelcomeSlide fetched={isReady} title="Weather Now" isOnPhone={isOnPhone}/>
      {isReady && <MainCard {...city} unit={unit} isOnPhone={isOnPhone}/>}
      {isReady && <InfoCard {...city} unit={unit} isOnPhone={isOnPhone}/>}
      <SearchBar setName={setName} ready={isReady} visible={true}/>
      <UnitSelector setUnit={setUnit}/>
    </Container>
  );
}

export default Home;
