import list from './OCL.js';

export const findCity = (word) => {
    if(word === undefined) return;
    const letters = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z', '‘'];
    let index = 0;

    word = word.toLowerCase();
    for(let i = 0; i < letters.length; i++){
        if(word.substring(0, 1).toLowerCase() === letters[i]){
            index = i;
            break;
        }
    }

    return list[index].filter(elem => elem.substring(0, word.length) === word);
}