import styled from "styled-components"; 
import { useRef } from "react";

const Container = styled.div`
    position: fixed;
    right: 1rem;
    top: 1rem;
`;

const Unit = styled.button`
    background-color: transparent;
    color: blueviolet;
    color: ${props => (props.active ? "blueviolet" : "#8f7dff")};
    font-weight: ${props => (props.active ? "bold" : "light")};
    border: none;   
    font-size: ${props => (props.active ? "1.5rem" : "1.2rem")};
    cursor: pointer;
`;


const UnitSelector = ({ setUnit }) => {
    let isCelsius = useRef(true);

    const handleClick = (unit, setUnit) => {
        if(unit === "C") isCelsius.current = true;
        else isCelsius.current = false;

        setUnit(unit);
    }

    return (
        <Container>
            <Unit onClick={() => {handleClick("C", setUnit)}} active={isCelsius.current}>C</Unit> 
            <label style={{color: "blueviolet", fontSize: "1.2rem", marginInline: "0.2rem"}}>/</label> 
            <Unit onClick={() => {handleClick("F", setUnit)}} active={!isCelsius.current}>F</Unit>
        </Container>
    )
}

export default UnitSelector
