import { animated, useSpring } from 'react-spring';
import styled from 'styled-components';
import Property from './Property';

const Container = styled.div`
    width: ${props => (props.isOnPhone ? "230px" : "400px")};
    height: 90%;
    background-color: #8475EB;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding: 1rem;
    padding-top: 4rem;
    overflow-y: hidden;
    overflow-x: hidden;
    transform: translateY(-3rem);
    z-index: 0;
    box-shadow: 0px 0px 10px gray;
`;

const InfoCard = ({ weather, main, clouds, wind, unit, isOnPhone }) => {
    const animateHeight = useSpring({
        from: { height: 0, opacity: 0 },
        to: { height: (isOnPhone ? 310 : 400), opacity: 1 },
        delay: 1000
    })
    return (
        <animated.div style={animateHeight}>
            <Container isOnPhone={isOnPhone}>
                {
                    (unit === "C") ?
                    <Property name="feels like" value={(main.feels_like - 273.15).toFixed(2)} add="°C" isOnPhone={isOnPhone}/> :
                    <Property name="feels like" value={((main.feels_like - 273.15) * 9/5 +32).toFixed(2)} add="°F" isOnPhone={isOnPhone}/>
                }
                <Property name="weather" value={weather[0].main} isOnPhone={isOnPhone}/>
                <Property name="humidity" value={main.humidity} add="%" isOnPhone={isOnPhone}/>
                <Property name="clouds" value={clouds.all} add="%" isOnPhone={isOnPhone}/>
                <Property name="pressure" value={main.pressure} add="hPa" isOnPhone={isOnPhone}/>
                <Property name="wind speed" value={wind.speed} add="m/s" isOnPhone={isOnPhone}/>
            </Container>
        </animated.div>
    )
}

export default InfoCard
