import WelcomeSlide from "./component/WelcomeSlide";
import SearchBar from "./component/SearchBar";
import styled from 'styled-components';
import { useEffect, useState } from 'react';
import WeatherItem from "./component/WeatherItem"; 
import { useSpring, animated } from "react-spring";
import UnitSelector from "./component/UnitSelector";

const Container = styled.section`
  height: 100vh;
  max-height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow-y: hidden;
`;

const Title = styled(animated.label)`
    text-decoration: underline;
    font-size: 2rem;
    color: blueviolet;
    position: absolute;
    top: 1.5rem;
    text-transform: capitalize;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
`;

const ListContainer = styled(animated.div)`
    width: 85vw;
    height: 81vh;
    padding-right: 0.5rem;
    position: absolute;
    padding-top: 1rem;
    overflow-x: hidden;
    /* border-top: #8475EB 3px solid;
    border-bottom: #8475EB 3px solid; */
`;

const Later = () => {
    const { REACT_APP_API_KEY: apiKey } = process.env;
    const [cityName, setCityName] = useState("london");
    const [weatherList, setWeatherList] = useState([]);
    const [unit, setUnit] = useState("C");
    const [isReady, setIsReady] = useState(false);
    let nameShown = cityName;
    const alternativeCity = "london";

    const moveIn = useSpring({
        from: {
            left: "-5rem",
            opacity: "0",
        },
        to: {
            left: "1rem",
            opacity: "1",
        },
        delay: 1400,
    });

    const moveUp = useSpring({
        from: {
            top: "30rem",
            opacity: "0",
        },
        to: {
            top:" 4.5rem",
            opacity: "1",
        },
        delay: 2000,
    });

    useEffect(() => {
        const url = `https://api.openweathermap.org/data/2.5/forecast?q=${cityName}&appid=${apiKey}`;

        const getInfo = async () => {
            const jsonInfo = await fetch(url);
            
            if(jsonInfo.ok){
                const cityInfo = await jsonInfo.json();
                setIsReady(true);
                setWeatherList(cityInfo.list);
                nameShown = cityInfo.city.name;
            } else {
                console.log("City Not Found!");
                setCityName(alternativeCity);
            }
        };
        getInfo();
        return () => {}
    }, [cityName]);

    const setName = (name, input) => {
        if(name === cityName){
            return
        }

        setIsReady(false);
        input.current.value= "";
        setCityName(name);
  }

    return (
        <Container> 
            <WelcomeSlide fetched={isReady} title="Weather Later"></WelcomeSlide>
            <SearchBar setName={setName} ready={isReady} visible={true}></SearchBar>

            { isReady && <Title style={moveIn}> {nameShown + ":"} </Title> }
            {
                isReady && 
                <ListContainer style={moveUp}>
                    {   weatherList.map(item => {
                            return <WeatherItem key={Math.floor(Math.random() * 10000000)} item={item} unit={unit}/>
                        })
                    }
                </ListContainer>
            }
            <UnitSelector setUnit={setUnit}/> 
        </Container>
    )
}

export default Later;
