import { useEffect, useState } from 'react';
import styled from 'styled-components';
import { animated, useSpring } from 'react-spring';
import Time from './Time';

const Container = styled.div`
    height: ${props => (props.isOnPhone ? '140px' : '180px')};
    min-width: 400px;
    min-width: ${props => (props.isOnPhone ? '200px' : '400px')};
    width: ${props => (props.isOnPhone ? "300px" : "500px")};

    background-image: linear-gradient(135deg, blueviolet, #3f1469);
    border-radius: 10px;
    box-shadow: 0px 0px 10px gray;
    padding: 1rem;
    color: white;
    position: relative;
    z-index: 2;
    margin-top: 3rem;
`;

const CityName = styled.h1`
    color: white;
    font-size: ${props => (props.isOnPhone 
        ? (props.longName ? '1.3rem' : '2rem') 
        : (props.longName ? '2.4rem' : '3.5rem') )};
    position: absolute;
    bottom: 1rem;
    left: 1.5rem;
    text-transform: uppercase;
    font-weight: bold;
`;

const TempLabel = styled.label`
    font-size: ${props => (props.isOnPhone ? "1.3rem" : "2rem")};;
    color: ghostwhite;
    position: absolute;
    top: ${props => (props.isOnPhone ? "1.5rem" : "2.1rem")};
    right: ${props => (props.isOnPhone ? "4.5rem" : "6.5rem")};
    font-weight: lighter;
`;

const Img = styled.img`
    height: ${props => (props.isOnPhone ? "55px" : "85px")};
    width: ${props => (props.isOnPhone ? "55px" : "85px")};
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    background-color: rgb(142, 79, 214);
    border-radius: 50%;
    border: black 1px solid;
`;

const CountryLabel = styled.label`
    margin-left: 0.5rem;
    color: #C9C2C5;
    font-size: ${props => (props.isOnPhone ? "1rem" : "1.2rem")};
`;

const MainCard = ({ name, main, weather, sys, timezone, unit, isOnPhone }) => {
    const [isLong, setIsLong] = useState(false);
    const temperature = (unit === "C") ? (main.temp - 273.15).toFixed(2) : ((main.temp - 273.15) * 9/5 +32).toFixed(2);
    const imgLink = `http://openweathermap.org/img/wn/${weather[0].icon}@2x.png`;

    useEffect(() => {
        if(name.length > 12) setIsLong(true);
        else setIsLong(false);
    }, [name]);

    const moveDown = useSpring({
        from: { marginTop: "0rem", position: "relative"},
        to: { marginTop: "5rem" },
        delay: 1000,
    });


    return (
        <animated.div style={moveDown}>
            <Container longName={isLong} isOnPhone={isOnPhone}>
                <Img src={imgLink} isOnPhone={isOnPhone}/>
                <TempLabel isOnPhone={isOnPhone}>
                    {temperature + ((unit === "C") ? "°C" : "°F")}
                </TempLabel>
                <CityName longName={isLong} isOnPhone={isOnPhone}>
                    {name}
                    <CountryLabel isOnPhone={isOnPhone}>{sys.country}</CountryLabel>
                </CityName>
            </Container>
                <Time timezone={timezone} isOnPhone={isOnPhone}/>
        </animated.div>
    )
}

export default MainCard
