import styled from "styled-components";
import { animated, useSpring } from "react-spring";

const normalWidth = "25vw";

const Container = styled(animated.div)`
    height: auto;
    width: ${normalWidth};
    background-color: ghostwhite;
    border: #8475EB 1px solid;
    border-radius: 15px;
    transition: height 200ms ease-out;
    text-align: center;
    position: absolute;
    bottom: 0.7rem;
    right: 2rem;
    padding-bottom: 1.5rem;
    z-index: -1;
    overflow: hidden;
`;

const City = styled.div`
    height: auto;
    width: 100%;
    color: #5d1e97;
    text-transform: capitalize;
    font-size: 1.5rem;
    border-bottom: #8475EB 1px solid;
    padding: 0.5rem;
    font-weight: 900;
    cursor: pointer;
    transition: background-color 100ms ease-out;

    &:hover, &:focus, &:active {
        color: ghostwhite;
        background-color: #8475EB;
    }
`;

const SuggestBox = ({ cities, setName, inputBox, listRef }) => {
    const selectedCities = [];

    for(let i = 0; i < 5; i++){
        if(cities[i]){
            selectedCities.push(cities[i]);
        }
    } 

    if(selectedCities.length < 1){
        inputBox.current.style.borderTopRightRadius = "15px";
        inputBox.current.style.borderTopLeftRadius = "15px";
    }

    const handleHover = (e) => {
        const list = listRef.current.querySelectorAll(`div`);
        list.forEach(elem => {
            elem.style.backgroundColor = "ghostwhite";
            elem.style.color = "#5d1e97";
        });

        e.target.style.backgroundColor = "#8475EB";
        e.target.style.color = "ghostwhite";
    };

    const handleLeave = (e) => {
        const list = listRef.current.querySelectorAll(`div`);
        list.forEach(elem => {
            elem.style.backgroundColor = "ghostwhite";
            elem.style.color = "#5d1e97";
        });
    };

    const moveUp = useSpring({
        from: { height: "0rem"},
        to: { height: (selectedCities.length) ? `${((selectedCities.length * 2.813) + 1.563)}rem` : "0rem" },
        config: { duration: 50},
    });

    return (
        <>
            <Container style={moveUp} ref={listRef} onMouseOver={handleHover} onMouseLeave={handleLeave}>
                {selectedCities.map(elem => <City key={Math.floor(Math.random() * 1000000)} onClick={(e) => setName(e.target.textContent, inputBox)}>{elem}</City>)}
            </Container>
        </>
    )
}

export default SuggestBox;
